import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 路由懒加载
const Home = (resolve) => {
  import('@/views/home/index').then((module) => {
    resolve(module)
  })
}

const Login = (resolve) => {
  import('@/views/entry/login').then((module) => {
    resolve(module)
  })
}

const Register = (resolve) => {
  import('@/views/entry/register').then((module) => {
    resolve(module)
  })
}

const Forget = (resolve) => {
  import('@/views/entry/forget').then((module) => {
    resolve(module)
  })
}

const Classify = (resolve) => {
  import('@/views/classify/index').then((module) => {
    resolve(module)
  })
}

const Find = (resolve) => {
  import('@/views/find/index').then((module) => {
    resolve(module)
  })
}

const Cart = (resolve) => {
  import('@/views/cart/index').then((module) => {
    resolve(module)
  })
}

const Mine = (resolve) => {
  import('@/views/mine/index').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    // 根路径
    { path: '/', redirect: '/home', name: 'Index', component: Home },
    // 首页
    { path: '/home', name: 'Home', component: Home },
    // 登录
    { path: '/login', name: 'Login', component: Login },
    // 注册
    { path: '/register', name: 'Register', component: Register },
    // 忘记密码
    { path: '/forget', name: 'Forget', component: Forget },
    // 分类
    { path: '/classify', name: 'Classify', component: Classify },
    // 发现
    { path: '/find', name: 'Find', component: Find },
    // 购物车
    { path: '/cart', name: 'Cart', component: Cart },
    // 我的
    { path: '/mine', name: 'Mine', component: Mine },

    // 404
    { path: '*', name: 'NotFound', component: Login },
  ]
})



